import React from 'react';
import { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { setActivePanel, setActiveModal} from "../store/mainReducer"
import { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';

const Results = () => {
    const dispatch = useDispatch()
    const resultsPhotoUrl = useSelector(state => state.main.resultsPhotoUrl)
    const cardsIDs = useSelector(state => state.main.cardsIDs)
    const slideFirst = useSelector(state => state.main.cardsContent).find((item)=>item.id === cardsIDs[0])
    const slideSecond = useSelector(state => state.main.cardsContent).find((item)=>item.id === cardsIDs[1])
    const slideThird = useSelector(state => state.main.cardsContent).find((item)=>item.id === cardsIDs[2])
    const imagePath = 'assets/images/'

    console.log(slideFirst,slideSecond,slideThird)

    return (
        <div className="container results-page">
            <div className="container__content">
                <div className="cards-slider">
                    <Swiper
                        // install Swiper modules
                        modules={[Navigation]}
                        slidesPerView={1}
                        navigation
                    >
                        <SwiperSlide>
                            <div className="cards-slide">
                                <div className="cards-slide__image-block">
                                    <img width="250" height="363" src={`${imagePath}/cards/${cardsIDs[0]}.jpg`} alt="first-card" />
                                    <p>{slideFirst.caption}</p>
                                </div>
                                <p className="cards-slide__text">{slideFirst.text}</p>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="cards-slide">
                                <div className="cards-slide__image-block">
                                    <img width="250" height="363" src={`${imagePath}/cards/${cardsIDs[1]}.jpg`} alt="second-card" />
                                    <p>{slideSecond.caption}</p>
                                </div>
                                <p className="cards-slide__text">{slideSecond.text}</p>
                            </div>
                        </SwiperSlide>
                        <SwiperSlide>
                            <div className="cards-slide">
                                <div className="cards-slide__image-block">
                                    <img width="250" height="363" src={`${imagePath}/cards/${cardsIDs[2]}.jpg`} alt="third-card" />
                                    <p>{slideThird.caption}</p>
                                </div>
                                <p className="cards-slide__text">{slideThird.text}</p>
                            </div>
                        </SwiperSlide>
                    </Swiper>
                </div>
                <div className="results-page__buttons">
                    <button className="button" onClick={()=>dispatch(setActiveModal('share'))}>Поделиться с друзьями</button>
                    <button className="results-page__reload-button" onClick={()=>dispatch(setActivePanel('processing'))}>Разложить карты снова</button>
                </div>
            </div>
        </div>
    );
}

export default Results;

