import React from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { setActivePanel, setCardsIDs } from "../store/mainReducer"

const Processing = () => {
    const dispatch = useDispatch()
    const imagePath = 'assets/images/'

    useEffect(() => {
        let count = 0;

        const counting = setInterval(function(){
            if(count < 101) {
                document.querySelector('.processing-page__value').innerHTML = count + '%';
                count++
            } else {
                clearInterval(counting)
                dispatch(setActivePanel('results'))
            }
        }, 30);

        const ids = [];

        const firstCardValidIDs = [1,4,7,10,14,17,20,23,26,29,32,35]
        const secondCardValidIDs = [3,6,9,12,15,18,21,24,27,30,33,36]
        const thirdCardValidIDs = [2,5,8,11,13,16,19,22,25,28,31,34]

        while(ids.length < 3){

            if(ids.length === 0){
                let picID = Math.floor(Math.random() * 35) + 1;
                if(firstCardValidIDs.indexOf(picID) !== -1){
                    ids.push(picID)
                }
            }

            if(ids.length === 1){
                let picID = Math.floor(Math.random() * 35) + 1;
                if(secondCardValidIDs.indexOf(picID) !== -1){
                    ids.push(picID)
                }
            }

            if(ids.length === 2){
                let picID = Math.floor(Math.random() * 35) + 1;
                if(thirdCardValidIDs.indexOf(picID) !== -1){
                    ids.push(picID)
                }
            }

        }

        dispatch(setCardsIDs(ids))
    }, []);


    return (
        <div className="container processing-page">
            <p className="caption processing-page__value">0%</p>
            <p className="caption processing-page__caption">Тянем карты<br/>из колоды...</p>
            <div className="processing-page__rails">
                <div className="processing-page__rail1">
                    <img src={`${imagePath}/loading-rail1.png`} width="1200" height="202" alt="cards" />
                    <img src={`${imagePath}/loading-rail1.png`} width="1200" height="202" alt="cards" />
                </div>
                <div className="processing-page__rail2">
                    <img src={`${imagePath}/loading-rail2.png`} width="1200" height="202" alt="cards" />
                    <img src={`${imagePath}/loading-rail2.png`} width="1200" height="202" alt="cards" />
                </div>
            </div>
        </div>
    );
}


export default Processing;

